  right: 10px;
  height: 80px;
  width: 150px;
  animation: upAndDown 5s infinite linear;
  background-image: url(../../assets/images/poisson3.svg);
  background-repeat: no-repeat;
}

@keyframes upAndDown {
  0% {
    transform: translateY(0px);
  }
  25% {
    transform: translateY(5px);
  }
  75% {
    transform: translateY(-5px);
  }
  100% {
    transform: translateY(0px);
  }
}

.section--blue .sac {
  height: 125px;
  width: 150px;
  right: 20px;
  background-image: url(../../assets/images/sac.svg);
  background-repeat: no-repeat;
