}

@keyframes sacFloat {
  0% {
    transform: translate(0px, -30px) rotate(0deg);
  }
  25% {
    transform: translate(15px, -15px) rotate(-10deg);
  }
  50% {
    transform: translate(0) rotate(-5deg);
  }
  75% {
    transform: translate(-15px, -15px) rotate(-10deg);
  }
  100% {
    transform: translate(0px, -30px) rotate(0deg);
  }
}

.section--blue .bouteille {
  left: 30px;
  bottom: 10px;
  height: 90px;
  width: 50px;
  transform: rotate(-30deg);
  background-image: url(../../assets/images/bouteille.svg);
  background-repeat: no-repeat;
  animation: boutFloat 10s infinite linear;
}

@keyframes boutFloat {
  0% {
    transform: translate(0) rotate(140deg);
  }
  25% {
    transform: translate(-10px, 15px) rotate(150deg);
  }
  75% {
    transform: translate(4px, 10px) rotate(130deg);
  }
  100% {
    transform: translate(0) rotate(140deg);
  }
}

