  .menu__icon {
    height: 34px;
    display: block;
    margin: 10px auto 0 auto;
    vertical-align: middle;
  }
}

@media (min-width: 1000px) {
  .menu {
    position: absolute;
    top: 0;
    display: block;
    background-color: inherit;
  }
  .menu .active a {
    color: #5676EF;
  }
  .menu__liste {
    padding-top: 25px;
    flex-direction: row;
    justify-content: center;
  }
  .menu__el {
    margin: 0 35px;
  }
  .menu__el a {
    color: #9DAEFE;
    display: inline-block;
    position: relative;
    margin: 0 5px;
  }
  .menu__el a:after {
    content: "";
    position: absolute;
    display: none;
    bottom: -5px;
    right: 0;
    height: 14px;
    width: 100%;
    background-image: url(../../assets/images/feuille.svg);
    background-repeat: no-repeat;
    background-position: center;
  }
  .menu__el a:hover:after {
    display: block;
  }
  .menu__el a:hover, .menu__el a:focus {
    color: #5676EF;
    transition: .4s ease-out;
    transform: translateY(-2px);
  }
  .menu__p {
    margin: 10px 0;
  }
  .menu__p--small {
    display: none;
  }
}

@media (min-width: 1800px) {
  .menu .menu__liste {
    padding-top: 50px;
  }
  .menu .menu__el {
    margin: 0 60px;
  }
}
